import React from 'react'

const Header = function() {

  const style = {
    backgroundColor: "#242423",
    width: "100%",
    padding: "10px",
    boxSizing: "border-box"
  }

  return (
    <div style={style}>
      <h1>React Particle Backgrounds</h1>
    </div>
  )
}

export default Header
